              <li>Gestor: La Oficina Asesora de Planeacin llevar el control del registro de las bases de datos con informacin personal que hay en GRUPO PINEAL S.A.S y apoyara el ingreso de la informacin en el Registro Nacional de Base de Datos.</li>
